// app/(product)/preview/page.tsx
"use client";
import React from "react";

import { HeroSection } from "@/components/landing-sections/hero";
import { FeaturesSection } from "@/components/landing-sections/features";
import { RichTextContentSection } from "@/components/landing-sections/rich-text-content";
import { HowItWorksSection } from "@/components/landing-sections/how-it-works";
import { ImageGallerySection } from "@/components/landing-sections/image-gallery";
import { SingleImageSection } from "@/components/landing-sections/single-image";
import { VideoSection } from "@/components/landing-sections/video";
import { TestimonialsSection } from "@/components/landing-sections/testimonials";
import { ClientLogosSection } from "@/components/landing-sections/client-logos";
import { FAQSection } from "@/components/landing-sections/faq";
import { PricingSection } from "@/components/landing-sections/pricing";
import { CTABannerSection } from "@/components/landing-sections/cta-banner";

const sectionComponents: Record<string, React.ComponentType<any>> = {
  hero: HeroSection,
  features: FeaturesSection,
  text_content: RichTextContentSection,
  how_it_works: HowItWorksSection,
  image_gallery: ImageGallerySection,
  single_image: SingleImageSection,
  video: VideoSection,
  testimonials: TestimonialsSection,
  logos: ClientLogosSection,
  faq: FAQSection,
  pricing: PricingSection,
  cta_banner: CTABannerSection,
};

interface PreviewPageProps {
  data?: {
    sections: Array<{
      id: number;
      global_section: {
        key: string;
        name: string;
      };
      content: Record<string, any>;
      is_published: boolean;
    }>;
  };
}

export default function ProductLandingPreview({ data = { sections: [] } }: PreviewPageProps) {
  const { sections } = data;

  return (
    <div className="min-h-screen bg-background">
      {sections.length === 0 ? (
        <div className="flex items-center justify-center h-screen">
          <p className="text-muted-foreground text-lg">
            No published sections to preview yet.
          </p>
        </div>
      ) : (
        sections.map((section) => {
          const SectionComponent = sectionComponents[section.global_section.key];

          if (!SectionComponent) {
            console.warn(`No component found for section key: ${section.global_section.key}`);
            return (
              <section key={section.id} className="py-32 bg-gray-100 text-center">
                <p className="text-muted-foreground text-xl">
                  [Missing component: {section.global_section.name} ({section.global_section.key})]
                </p>
              </section>
            );
          }

          // Spread content directly – matches how we defined props in each component
          return <SectionComponent key={section.id} content={section.content} />;
        })
      )}
    </div>
  );
}