import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion";

interface FAQProps {
  content: {
    title?: string;
    faqs?: string; // Question|Answer per line
  };
}

export const FAQSection = ({ content }: FAQProps) => {
  const { title = "Frequently Asked Questions", faqs = "" } = content;
  const faqList = faqs.split("\n").filter(Boolean).map((f, i) => {
    const [q, a] = f.split("|");
    return { question: q, answer: a, value: `item-${i}` };
  });

  return (
    <section className="py-20">
      <div className="max-w-4xl mx-auto px-6">
        <h2 className="text-4xl font-bold text-center mb-12">{title}</h2>
        <Accordion type="single" collapsible>
          {faqList.map((faq) => (
            <AccordionItem key={faq.value} value={faq.value}>
              <AccordionTrigger>{faq.question}</AccordionTrigger>
              <AccordionContent>{faq.answer}</AccordionContent>
            </AccordionItem>
          ))}
        </Accordion>
      </div>
    </section>
  );
};