interface PricingProps {
    content: {
      title?: string;
      plans?: string; // Name|Price|Features (comma separated)|Popular
    };
  }
  
  export const PricingSection = ({ content }: PricingProps) => {
    const { title = "Choose Your Plan", plans = "" } = content;
    const planList = plans.split("\n").filter(Boolean).map((p) => {
      const [name, price, features, popular] = p.split("|");
      return { name, price, features: features?.split(",") || [], popular: popular === "true" };
    });
  
    return (
      <section className="py-20 bg-gray-50">
        <div className="max-w-7xl mx-auto px-6">
          <h2 className="text-4xl font-bold text-center mb-12">{title}</h2>
          <div className="grid md:grid-cols-3 gap-8">
            {planList.map((plan, i) => (
              <div
                key={i}
                className={`bg-white p-8 rounded-2xl shadow-lg ${plan.popular ? "ring-4 ring-primary scale-105" : ""}`}
              >
                {plan.popular && <div className="bg-primary text-white text-center py-2 rounded-t-xl -m-8 mb-8">Most Popular</div>}
                <h3 className="text-2xl font-bold mb-4">{plan.name}</h3>
                <p className="text-4xl font-bold mb-8">${plan.price}<span className="text-lg font-normal">/month</span></p>
                <ul className="space-y-4 mb-8">
                  {plan.features.map((f, j) => (
                    <li key={j} className="flex items-center gap-3">
                      <span className="text-green-500">✓</span> {f}
                    </li>
                  ))}
                </ul>
              
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  };