interface HowItWorksProps {
    content: {
      title?: string;
      steps?: string; // Number|Title|Description per line
    };
  }
  
  export const HowItWorksSection = ({ content }: HowItWorksProps) => {
    const { title = "How It Works", steps = "" } = content;
    const stepList = steps.split("\n").filter(Boolean).map((s) => {
      const [num, title, desc] = s.split("|");
      return { num, title, desc };
    });
  
    return (
      <section className="py-20">
        <div className="max-w-7xl mx-auto px-6">
          <h2 className="text-4xl font-bold text-center mb-16">{title}</h2>
          <div className="grid md:grid-cols-3 gap-12">
            {stepList.map((step, i) => (
              <div key={i} className="text-center">
                <div className="text-6xl font-bold text-primary mb-4">{step.num || i + 1}</div>
                <h3 className="text-2xl font-semibold mb-4">{step.title}</h3>
                <p className="text-muted-foreground">{step.desc}</p>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  };