interface SingleImageProps {
    content: {
      image_url?: string;
      caption?: string;
    };
  }
  
  export const SingleImageSection = ({ content }: SingleImageProps) => {
    const { image_url = "", caption = "" } = content;
  
    return (
      <section className="py-20">
        <div className="max-w-7xl mx-auto px-6">
          <img src={image_url} alt={caption} className="w-full rounded-xl shadow-xl" />
          {caption && <p className="text-center mt-6 text-muted-foreground">{caption}</p>}
        </div>
      </section>
    );
  };