interface FeaturesProps {
    content: {
      title?: string;
      items?: string; // one per line
    };
  }
  
  export const FeaturesSection = ({ content }: FeaturesProps) => {
    const { title = "Key Features", items = "" } = content;
    const featureList = items.split("\n").filter(Boolean);
  
    return (
      <section className="py-20 bg-gray-50">
        <div className="max-w-7xl mx-auto px-6">
          <h2 className="text-4xl font-bold text-center mb-12">{title}</h2>
          <div className="grid md:grid-cols-3 gap-8">
            {featureList.map((item, i) => (
              <div key={i} className="bg-white p-8 rounded-xl shadow-md text-center">
                <div className="w-16 h-16 bg-primary/10 rounded-full mx-auto mb-6 flex items-center justify-center">
                  <span className="text-2xl font-bold text-primary">{i + 1}</span>
                </div>
                <p className="text-lg font-medium">{item}</p>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  };