import { Button } from "@/components/ui/button";

interface HeroProps {
  content: {
    heading?: string;
    subheading?: string;
    cta_text?: string;
    cta_link?: string;
    background_image?: string;
    show_cta?: boolean;
  };
}

export const HeroSection = ({ content }: HeroProps) => {
  const {
    heading = "Your Amazing Product",
    subheading = "Build something incredible",
    cta_text = "Get Started",
    cta_link = "#",
    background_image,
    show_cta = true,
  } = content;

  return (
    <section
      className="relative min-h-screen flex items-center justify-center text-center bg-cover bg-center"
      style={background_image ? { backgroundImage: `url(${background_image})` } : {}}
    >
      <div className="absolute inset-0 bg-black/50" />
      <div className="relative z-10 max-w-4xl px-6">
        <h1 className="text-5xl md:text-6xl font-bold text-white mb-6">
          {heading}
        </h1>
        <p className="text-xl md:text-2xl text-white/90 mb-10">
          {subheading}
        </p>
        {show_cta && (
          <Button size="lg" asChild>
            <a href={cta_link}>{cta_text}</a>
          </Button>
        )}
      </div>
    </section>
  );
};