interface GalleryProps {
    content: {
      title?: string;
      images?: string; // one URL per line
    };
  }
  
  export const ImageGallerySection = ({ content }: GalleryProps) => {
    const { title = "Gallery", images = "" } = content;
    const imageList = images.split("\n").filter(Boolean);
  
    return (
      <section className="py-20 bg-gray-50">
        <div className="max-w-7xl mx-auto px-6">
          {title && <h2 className="text-4xl font-bold text-center mb-12">{title}</h2>}
          <div className="columns-2 md:columns-3 gap-6">
            {imageList.map((src, i) => (
              <img
                key={i}
                src={src}
                alt={`Gallery image ${i + 1}`}
                className="mb-6 rounded-lg shadow-md w-full"
              />
            ))}
          </div>
        </div>
      </section>
    );
  };