interface LogosProps {
    content: {
      title?: string;
      logos?: string; // URLs one per line
    };
  }
  
  export const ClientLogosSection = ({ content }: LogosProps) => {
    const { title = "Trusted by leading companies", logos = "" } = content;
    const logoList = logos.split("\n").filter(Boolean);
  
    return (
      <section className="py-20 bg-gray-50">
        <div className="max-w-7xl mx-auto px-6">
          <h2 className="text-3xl font-bold text-center mb-12 text-muted-foreground">{title}</h2>
          <div className="flex flex-wrap justify-center items-center gap-12">
            {logoList.map((src, i) => (
              <img
                key={i}
                src={src}
                alt={`Client logo ${i + 1}`}
                className="h-12 grayscale hover:grayscale-0 transition duration-300 opacity-70 hover:opacity-100"
              />
            ))}
          </div>
        </div>
      </section>
    );
  };