interface TestimonialsProps {
    content: {
      title?: string;
      quotes?: string; // Name|Company|Quote per line
    };
  }
  
  export const TestimonialsSection = ({ content }: TestimonialsProps) => {
    const { title = "What Our Customers Say", quotes = "" } = content;
    const quoteList = quotes.split("\n").filter(Boolean).map((q) => {
      const [name, company, quote] = q.split("|");
      return { name, company, quote };
    });
  
    return (
      <section className="py-20">
        <div className="max-w-7xl mx-auto px-6">
          <h2 className="text-4xl font-bold text-center mb-12">{title}</h2>
          <div className="grid md:grid-cols-3 gap-8">
            {quoteList.map((t, i) => (
              <div key={i} className="bg-white p-8 rounded-xl shadow-lg">
                <p className="text-lg italic mb-6">"{t.quote}"</p>
                <div className="font-semibold">{t.name}</div>
                <div className="text-muted-foreground">{t.company}</div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  };